import BaseRequest from "@/http/newRequest.js";
const api = new BaseRequest("");

// 1. 
export const getUserID = () =>
  api.post("/aiguide/v2/chatExt/session/save", { name: "" });

// 2. 
export const getRecommendQuestions = (params) =>
  api.post("/aiguide/v2/chatExt/message/questionPage", params);

// 3. 
export const transferQuest = (params) =>
  api.postMul("/aiguide/v2/chatExt/speech/uploadVoice", params);

// 4. no-stream
export const getAnswer = (params) =>
  api.post("/aiguide/v2/chatExt/message/text", params);

// 5. 
export const saveFeedback = (params) =>
  api.post("/aiguide/v2/chatExt/message/saveFeedback", params);

export const getDetail = (params) =>
  api.postForm("/virtualAccount/detail", params);

export const getWhiteList = (params) =>
  api.post("/aiguide/v2/chatExt/session/eclicktechDocs", params);

export const getDownloadUrl = (params) =>
  api.get("/aiguide/v2/chatExt/session/downloadUrl", params);
