    },
    BpsVisible: {
      type: Boolean,
    },
  },
  setup(props, ctx) {
           const cancelToken = axios.CancelToken;
    const source = cancelToken.source();   
    onMounted(() => {

    //    pointerBox.addEventListener('touchmove', startRecording(event))
    
      toBottom();

      getUserCode()

      getCommonQuests('',false,1)

     
      
      bus.on('STREAM-RESULT',(res)=>{
        console.log('SESSION_CHANGED: ', res);
         const mL = state.mainList.length-1 
               console.log('state.mainList: ', state.mainList,mL);
         if(mL>-1&&state.mainList[mL]?.stopPending) return;
        // 
        if(res.error){
          state.errorText = res.text  
          setTimeout(() => {
            state.errorText = ''
            state.mainList.splice(mL,1)
          }, 3000);
        }else{
              if(res.loading){
                // ...
                // state.think = false
                state.mainList[mL].question = res.text 
                state.mainList[mL].think = false
              }else{
                // 
                state.think = false
                if(mL===-1) state.mainList=[{ question:'',answerText:'',stopShowword:false,think:false,id:mL+1}]
                if(res.text){
                  console.log('state.mainList: ', state.mainList);
                  state.mainList[mL].question = res.text  
                  state.mainList[mL].think = false
                  !res.error&&getTheAnswer(res.text,'',res.speechToTextTaskId,3,mL)
                }else {
                  if(mL<0){
                     state.mainList=[{ question:'',answerText:'',stopShowword:false,think:false,id:mL+1}]
                  }else  {
                    state.mainList[mL]['question'] = ''  
                    state.mainList[mL].think = false
                  }
                   
                    state.answerShow = false
                }
                
              }
        }

    
      })

      // window.addEventListener("scroll", handleScroll);


    });
    const iconState = reactive({
      preImg,
      closeImg,
      jingImg,
      mikeIcon,
      changeIcon,
      listIcon,
      sendIcon,
      sendActiveIcon,
      keyboardIcon,
      baipishuIcon,
      guanweiIcon,
      shangwuIcon,
      stopIcon,
      zanIcon,
      zanAIcon,
      caiIcon,
      caiAIcon,
      toAskIcon,
      activeBaipishuIcon,
      recordindIcon,
      volumeImg,
      mutedImg,
      baipishuPng,
      guanweiPng,
      shangwuPng,
      isRecordPng,
      recordingPng,
      think1Png,
      thinkPng,
      logohiPng,
      logohijPng
    });

    const state = reactive({
      questions: [
        { label: "" },
        { label: "" },
        { label: "ESG" },
      ],
      commonQuesQuery:{
        current:1,
        pages:1,
        size:3,
      },
      elseQuestions:[], //
      mainList:[],
      qrVisiable: false, //
      navActive:'',//
      sendTypeIsRecord: false, //false: true
      sendText: "",
      asking: false,  //
      think: false, //
      pending: false, //
      answerShow: true, //
      stopShowword: false, //

      timer:'',//
      answerTextSet:'', //

      IsRecordIng: false,
      BPSVisible: false,
      
      recordData:'',//

      animateHide: false, //css
      errorText: '', //
      mediaAuth:true,//

      curQuesISCOMMON: false, //
      muted: false,

      qrTypeIsTop: true,
      showTop: true,//

      hover_navActive: '',
      pendingOneSecond:true,

       isShowBar: false,
       scrollTimer:'',
       opacity0Flag: false,

       isSingleClick:false
      
    });



    const closeForm = () => {
      console.log('closeForm: ', );
      ctx.emit("closeForm", !props.oneSreen,'fromRobot');
    };

    

    //  start

    // 
   const startRecording = (event)=>{
    console.log(': ', event,!state.recordData);
    if(state.mediaAuth&&state.recordData){
      console.log('state.recordData: ', state.recordData);
      // state.recordData= Recorder({
      //   type: "wav", //wav
      //   sampleRate: 46000, //
      //   bitRate: 24, //
      // });
      //       // 
      state.recordData.start()
      state.IsRecordIng = true
      //  // 
      // state.recordData.recorder = new MediaRecorder(state.recordData.mediaStream);
      // // 
      // state.recordData.recorder.ondataavailable = (event) => {
      //   if (event.data.size > 0) {
      //     state.recordData.chunks.push(event.data);
      //   }
      // };
      // // 
      // state.recordData.recorder.start();
      console.log('...');
    }else getAuth(state.isSingleClick?false:true)
   }

   

    

  //   
    const stopRecording = (event)=>{
      if(!state.mediaAuth) return;
        console.log(': ',event );

        // state.recordData&&state.recordData.close()
        // 
         
          nextTick(()=>{
             const cancelBox = document.getElementsByClassName("cancelZero")[0]             
             console.log('cancelBox: ', cancelBox?.offsetHeight,cancelBox?.height);
          const pointerBox = document.getElementsByClassName("pointerBox")[0]
        
        
         
          let isYInCancelbox='',isXInCancelbox=''
            if(!props.oneSreen){
               const screenH = pointerBox.offsetHeight
               const endY =screenH-event.offsetY
               const cancelMaxY =screenH-cancelBox?.offsetTop
               const cancelMinY =screenH-cancelBox?.offsetHeight-cancelBox?.offsetTop
              console.log('cancelMaxY: ', cancelMaxY,endY,cancelMinY);
            isYInCancelbox = ( endY > cancelMinY) && (endY < cancelMaxY )
            isXInCancelbox =  (event?.offsetX > cancelBox?.offsetLeft ) && (event?.offsetX < (cancelBox.offsetLeft+cancelBox.offsetWidth) )
          }else{
            
             // Ywindow.screen.height-cancelBox?.offsetTop  window.screen.height-cancelBox?.offsetHeight-cancelBox?.offsetTop
              const endY = event.changedTouches?.[0].pageY 
              const cancelMaxY = window.screen.height - 180
               const cancelMinY = window.screen.height-cancelBox.offsetHeight-180

               const endX = event.changedTouches?.[0].pageX

               console.log('cancelMinY: ', endY,cancelMaxY,cancelMinY);
                console.log('cancelMinX: ', endX,cancelBox.offsetLeft+cancelBox.offsetWidth,cancelBox.offsetLeft);

