          //   document.body.removeChild(a);
          //   // URL.revokeObjectURL(url);
          //   // console.log('url: ', url);

          //   getTheTransQuet(blob)
          // };
          try {
            
        
              state.recordData.stop((blob, duration) => {
                    
                  // blob
                  // audioBlod = blob
                  var localUrl = (window.URL || webkitURL).createObjectURL(blob);
                  console.log("",blob,localUrl,":"+duration+"ms");
                  // const a = document.createElement('a');
                  // a.href = localUrl;
                  // a.download = 'recording.wav';
                  // a.click();
                if(duration>1200){
                      getTheTransQuet(blob)              
                      state.recordData&&state.recordData?.close()
                      state.recordData = null
                      // getAuth()// 
                }else{
                  state.recordData&&state.recordData?.close()
                      state.recordData = null
                    state.errorText = '' 
                    setTimeout(() => {
                      state.errorText = ''
                    }, 1800);
                }

                
                  
              },(err) => {
                  console.error("" + err)
                state.recordData.close()
                state.recordData = null
                
                getAuth(false)// 
                
          })
            } catch (error) {
               state.IsRecordIng = false
               state.recordData&&state.recordData?.close()
             state.recordData = null
            
          }



        }
          })

      
   }

   const getTheTransQuet = async(file)=>{
     state.asking = true
     state.IsRecordIng = false
     state.think = true
     state.answerShow = false
    const mL = state.mainList.length-1 
    console.log('state.mainList: ', state.mainList);
      if(mL===-1) state.mainList=[{ question:'',answerText:'',stopShowword:false,think:true,id:mL+1}]
      else state.mainList.push({
        question:'',answerText:'',stopShowword:false,think:true,id:state.mainList.length+1
      })
      toBottom()
    
    console.log('file: ', file,{multipartFile:file,conversationId:sessionStorage.getItem("conversationId")});
    await transferQuest({multipartFile:file,conversationId:sessionStorage.getItem("conversationId")})


    // let {data} =  await transferQuest({multipartFile:file,conversationId:sessionStorage.getItem("conversationId")})
    // console.log('data: ', data);
    // transferQuest({multipartFile:file,conversationId:sessionStorage.getItem("conversationId")}).then((res)=>{
    //   console.log('res: ', res);

    // })
   }


  //  
    const resetRecord = ()=>{
        console.log(': ', );
          state.recordData=''
          state.IsRecordIng = false
   }

   const changeSendType = ()=>{
      state.sendTypeIsRecord = !state.sendTypeIsRecord
    if(!state.sendTypeIsRecord) autoFocus()
    // state.IsRecordIng = true
    if(state.sendTypeIsRecord)  getAuth()// 
    sessionStorage.setItem("stopThink",false)
   }

  const getAuth = async(isStart = false) => {
      const {code,msg,rec}= await getMediaAuth()
      console.log('code,msg: ', code,msg,isStart,state.isSingleClick);
      state.recordData = code==0?rec:'',// 
      state.mediaAuth = code===0
      sessionStorage.setItem("stopThink",false)
      
        
         state.isSingleClick = false
      if(code==0){
        if(isStart){
          // 
          state.recordData.start()
          state.IsRecordIng = true
        }else{
          state.IsRecordIng = false
           state.recordData&&state.recordData?.close()
             state.recordData = null
        }
         
      }
      if(code===1) {
         state.IsRecordIng = false
        state.errorText = msg
        setTimeout(() => {
          state.errorText = ''
        }, 3000);
      }
    }

   //  end




    const navClick = (type,isTop=false) => {
         console.log('state.navActive: ', state.navActive);
         state.qrVisiable = false
         if(type==1){
            state.navActive = type
          // 
           logs(9)//location.protocol=="https:"&&logs(9)
           state.BPSVisible = true
          // if(!props.oneSreen) state.BPSVisible = true
          // else window.open("https://www.eclicktech.com.cn/html/report");
         }else if(type==3){
            state.navActive = type
          // 
           logs(3)
           ctx.emit("toLinkIndex",1,state.oneSreen,'fromRobot_Form');

         }else {
          state.qrTypeIsTop = isTop
            if(state.navActive==type){
              state.qrVisiable = false
               state.navActive = ''
            }else{
               state.navActive = type
               state.qrVisiable = true
             if(!isTop)  setTimeout(() => {
                   toBottom(isTop)
               }, 200);
               
                logs(4) 
            }
           
         }
    };

    const closeQr = (e)=>{
      console.log('e: ', e);
       store.commit("mSetDownInfo", {});
      if(e=='baipishu'){
        state.animateHide = true
        setTimeout(() => {
          state.animateHide = false
          state.BPSVisible = false
        }, 0);
      }else{
       state.qrVisiable = false
       state.navActive = ''

      }
    }

    const toBottom = (toTop) => {
      const scrollContainer = document.querySelector(".scrollCont");


      nextTick(() => {
        let _totalH = document.querySelector(".scrollCont").scrollHeight;
        scrollContainer.scrollTop = toTop? 0: _totalH;
        state.showTop = toTop
      });
    };

    
    const toPreVideo = (toTop) => {
       ctx.emit("toPreVideo");
    }
       const handleClick = () => {
        console.log('handleClick: ', );
          state.IsRecordIng = false
               state.recordData&&state.recordData?.close()
             state.recordData = null
              state.isSingleClick = true
    }
          const handleDoubleClick = () => {
              console.log('handleClick: ', );
              state.isSingleClick = false
                state.IsRecordIng = false
               state.recordData&&state.recordData?.close()
             state.recordData = null

    }

    const toLinkIndex = (e,from='fromRobot') => {
      console.log('from: ', from);
       state.animateHide = true
        setTimeout(() => {
          state.animateHide = false
          state.BPSVisible = false
        }, 1000);
       ctx.emit("toLinkIndex",e,state.oneSreen,from);
    }
    
    const toPolicy = (type)=>{
       logs(13);// location.protocol=="https:"&&logs(13);
      window.open(location.origin+'/dist/#/policy?type='+type)
    }

    // 1. 
    const getUserCode= async()=>{
    //  let {data} =  await getUserCode()
      getUserID().then((res)=>{
        sessionStorage.setItem("conversationId",res.data.conversationId) 
      })
    }

    // 2. 
    const getCommonQuests = async(parentId,isElse,currentPage)=>{
      console.log('parentId,isElse,currentPage: ', parentId,isElse,currentPage);
      // isElse: true:false
       const parmas = {
          parentId: isElse?parentId:'0',
          current: !isElse?currentPage:1,
          size: isElse?10:3,
       }
       let {data} =  await getRecommendQuestions(parmas)
       state[isElse?'elseQuestions':'questions'] = data.records
       if(isElse) setTimeout(() => {
         toBottom()
       }, 1000);
       if(!isElse){
        state.commonQuesQuery.current = currentPage
        state.commonQuesQuery.pages = data.pages
       }
    }

    const changeQuest=()=>{

        
        if( state.commonQuesQuery.pages> state.commonQuesQuery.current)  state.commonQuesQuery.current = state.commonQuesQuery.current+1
        else  state.commonQuesQuery.current =1
      
       getCommonQuests('',false,state.commonQuesQuery.current)
    }

    // 
    const sendQuestion = (questionText,row) => {
      console.log('questionText,row=', questionText,row);
      questionText = questionText.trim()
      if(!questionText) {
        state.errorText = ''
        state.sendText=''
        setTimeout(() => {
          state.errorText = ''
        }, 2000);
        return;
      }else autoFocus()
      const mL = state.mainList.length 
      state.mainList.push({
        question:questionText,answerText:'',stopShowword:false,think:false,id:mL+1
      })
      //  state.stopShowword = true
      state.answerTextSet = ''
      state.navActive = ''
      state.asking = true
      state.qrVisiable = false
      state.sendText=''
      getTheAnswer(questionText,row?.id,'',row?1:2, mL, row?.parentId=='0')
       toBottom()
    };

    const dynamicShow = (text,mIndex) => {
      const textLang_isEnglish = false//isEnglish(text)
      console.log('textLang_isEnglish: ', textLang_isEnglish);
      let index=0;
      const mL = state.mainList.length - 1
      state.mainList[mIndex].timer = setInterval(()=>{
         let textss = text
        state.mainList[mIndex].answerText += textLang_isEnglish?textss.split(' ')[index]:textss.charAt(index);

        index++;
        // state.mainList[mIndex].answerText = state.answerTextSet
        // console.log('index: ', index,state.answerTextSet,state.mainList)
        
          toBottom()
      //  if(state.IsRecordIng) toBottom();
        if (index == text.length) {
           state.stopShowword = false
           state.mainList[mIndex].stopShowword = false
          clearInterval(state.mainList[mIndex].timer);
          autoFocus()
        }
      }, 50);
    };

    const stopTextShow = (pending) => {
       const mL = state.mainList.length - 1
      // pending: 
      if(pending){
          source.cancel('');
          console.log('source: ', source);
          state.pending = false
          state.think = false
          state.stopShowword = false
          state.mainList[mL].stopPending = true

           if(state.sendTypeIsRecord){
             state.mainList[mL].think = false
             state.mainList[mL]['question'] = '' 
             sessionStorage.setItem("stopThink",true)

           }

      }else{
          state.mainList.forEach((E,index)=>{
            clearInterval(E.timer);
            if(mL==index)  E.stopShowword = true
          })
              // clearInterval(state.mainList[mL].timer);
              state.timer = null;
              state.stopShowword = true
              // state.mainList[mL].stopShowword = true
              setTimeout(() => {
                state.stopShowword = false
                toBottom()
              }, 100);
      }
      
                autoFocus()

  
    };



    // 3. 
    const getTheAnswer= async(quesText,questionId,speechToTextTaskId,type,mIndex,noParent)=>{
      console.log('noParent: ', noParent);
      // type: 123
   
       const parmas = {
          "questionType": type==1?1:2,
          "questionForm": {
            1:1,2:1,3:2
          }[type],
          "content": quesText,
       }
       if(questionId) parmas.questionId = questionId
       if(speechToTextTaskId) parmas.speechToTextTaskId = speechToTextTaskId

       state.pending = true
       state.stopShowword = true
      console.log('parmas: ', parmas);
       toBottom()
       state.elseQuestions = []
       let {code,data} =  await getAnswer(parmas)
       if(!state.pending) return;
       state.pending = false
       state.stopShowword = false
       if(code==200) {
        state.stopShowword = true
          // ...
          state.mainList[mIndex].answerId = data.answerId
          state.mainList[mIndex].questionId = data.questionId
          state.mainList[mIndex].audioFileUrl = data.audioFileUrl
          state.mainList[mIndex].originAnswer = data.answerText
          state.mainList[mIndex].answerText = ''
          state.mainList[mIndex].feedbackStatus = ''
           toBottom()
           state.answerShow = true
         dynamicShow(data.answerText,mIndex)
          if(type==1&&noParent) {
            state.curQuesISCOMMON = true
            state.elseQuestions = []
            setTimeout(() => {
                getCommonQuests(parmas.questionId,true,1)
            }, 1000);
          }
       }

    }

   const toFeedBack = async(index,type,item,isActive)=>{
    // isActive: 
    // type: 1: 2
  if(isActive) return;
    const { questionId,answerId} = item
     const parmas = {
        questionId,answerId,
        "feedbackStatus": type==1? 0:1    // 0- 1-
     }
     state.mainList[index].feedbackStatus= type
     let {code,data} =  await saveFeedback(parmas)
    //  if(code==200) 

   }
    

    const handleScroll = () => {
        let scrollContainer = document.querySelector(".scrollCont")

      var _offsetTop = scrollContainer.scrollTop;
        //   let _totalH = scrollContainer.scrollHeight;
        //   let is = !state.pending&&(!state.stopShowword)&&state.answerShow
        //   const pd = getPadBot()
        //   console.log('_offsetTop: ', _offsetTop,is,pd,_totalH);
        //   // || _totalH<689&&_offsetTop<85
        //   if(!is) return;
        //  setTimeout(() => {
        //      state.showTop = _offsetTop<20
        //     //  scrollContainer.scrollTop = _totalH  (pd<200? ( _offsetTop<95):( _offsetTop<pd) )
        //  }, 800);
        // 
      clearTimeout(state.scrollTimer);

      // 
      state.scrollTimer = setTimeout(function() {
        // 
        state.isShowBar = false;
      }, 400); // 

        state.isShowBar = true
        state.showTop =_offsetTop<20
          console.log('_offsetTop: ', _offsetTop,state.showTop);
    };


    const getPadBot = () => {
      let _pd = 98;
     

      if(state.answerShow&&state.stopShowword) {
        // if(state.pending) _pd = 268;
        // else 
        _pd = 138;
      }

       if(!state.mainList.length) {
        _pd = 50
        state.showTop = true
       }

        if(state.mainList.length&&(state.pending||state.think))  _pd = 158;
        // if(state.mainList.length&&state.pending&&state.think)  _pd = 238;
      // if(state.qrVisiable||!state.mainList.length?state.showTop: true) _pd = 328
      // if(state.qrVisiable&&!state.mainList.length&&!state.showTop) _pd = 328
      // // if(state.mainList.length&&!state.showTop) _pd = 173
      // if(state.mainList.length&&state.asking&&!state.showTop&&state.answerShow&&state.stopShowword&&!state.pending) _pd = 238
      //  if(state.mainList.length&&state.showTop) {
      //   _pd = _totalH.scrollHeight>640? 238:328
      //  }
      //   if(state.mainList.length&&!state.qrVisiable&&!state.showTop) _pd = 173
      //    if(state.mainList.length&&!state.qrVisiable&&!state.showTop&&state.answerShow&&state.stopShowword&&!state.pending) _pd = 238
       
      return _pd+(props.oneSreen?'pt':'px')
      };

    const handleMouseOver = (index, key) => {
      console.log('handleMouseOverindex, key: ', index, key);
      state['hover_'+key] = index
    };

   const handleMouseLeave = (index, key) => {
      console.log('handleMouseLeaveindex, key: ', index, key);
      state['hover_'+key] = '' 
    };

   const BpsHidevent = () => {
     state.BPSVisible = false
      store.commit("mSetDownInfo", {});
    };


    const autoFocus = ()=>{
      if(props.oneSreen) return;
      if(state.sendTypeIsRecord) return;
      
       nextTick(() => {
            let _filedInput = document.querySelector("input");
          console.log('autoFocus: ');
          setTimeout(() => {
                _filedInput.focus();
                _filedInput.autofocus = true
              
          }, 100);
       });
       
    }

    watch(
      () => state.pending,
      (newValue, oldValue) => {
        if(newValue) {
           state.pendingOneSecond = true
           setTimeout(() => {
              state.pendingOneSecond = false
           }, 1000);
        }
      }
    );

    watch(
      () => props.visiable,
      (newValue, oldValue) => {
        if(newValue) {
         autoFocus()
        }
      }
    );

  watch(
      () => props.BpsVisible,
      (newValue, oldValue) => {
        console.log('newValue, oldValue: ', newValue, oldValue,store.state.downInfo?.pdfsrc);
        state.BPSVisible = newValue||false

        if(newValue){
         if (store.state.downInfo?.pdfsrc&&store.state.downInfo?.pdfsrc?.length) {
            state.opacity0Flag = true
            setTimeout(() => {
              state.opacity0Flag = false
            }, 300);
         }
         
        }else{
             state.opacity0Flag = false
            state.animateHide = true
            setTimeout(() => {
              state.animateHide = false
              state.BPSVisible = false
            }, 1000);
        }
      }
    );

        watch(
      () => state.isSingleClick,
      (newValue) => {
        if(newValue) {
          console.log('true: ', newValue);
          setTimeout(() => {
               state.IsRecordIng = false
           state.recordData&&state.recordData?.close()
             state.recordData = null
             state.isSingleClick = false
          }, 200);
        
        }
      }
    );
    
    

    return {
      ...toRefs(state),
      ...toRefs(iconState),
      toBottom,
      dynamicShow,
      stopTextShow,
      sendQuestion,
      getTheAnswer,
      navClick,
      closeQr,
      startRecording,
      stopRecording,
      resetRecord,
      changeSendType,
      toPreVideo,
      getAuth,
      toLinkIndex,
      toPolicy,
      getCommonQuests,
      changeQuest,
      toFeedBack,

      getUserCode,
      closeForm,
      handleScroll,
      getPadBot,
      handleMouseOver,
      handleMouseLeave,
      BpsHidevent,

      handleDoubleClick,
      handleClick


     
    };
  },
});
