(function(factory){
	var browser=typeof window=="object" && !!window.document;
	var win=browser?window:Object; //RecorderObject
	var rec=win.Recorder,ni=rec.i18n;
	factory(rec,ni,ni.$T,browser);
}(function(Recorder,i18n,$T,isBrowser){
"use strict";

Recorder.prototype.enc_wav={
	stable:true,fast:true
	,getTestMsg:function(){
		return $T("gPSE::816pcm44wav16wav44pcmwav44");
	}
};

var NormalizeSet=function(set){
	var bS=set.bitRate,b=bS==8?8:16;
	if(bS!=b) Recorder.CLog($T("wyw9::WAV Info: {1}{2}",0,bS,b),3);
	set.bitRate=b;
};

Recorder.prototype.wav=function(res,True,False){
	var This=this,set=This.set;
	
	NormalizeSet(set);
	var size=res.length,sampleRate=set.sampleRate,bitRate=set.bitRate;
	var dataLength=size*(bitRate/8);
	
	//wav
	var header=Recorder.wav_header(1,1,sampleRate,bitRate,dataLength);
	var offset=header.length;
	var bytes=new Uint8Array(offset+dataLength);
	bytes.set(header);
	
	// 
	if(bitRate==8) {
		for(var i=0;i<size;i++) {
			//168 https://blog.csdn.net/sevennight1989/article/details/85376149 blqw
			var val=(res[i]>>8)+128;
			bytes[offset++]=val;
		};
	}else{
		bytes=new Int16Array(bytes.buffer);//
		bytes.set(res,offset/2);
	};
	
	True(bytes.buffer,"audio/wav");
};

/**
wavUint8Arrayformat=14446
format: 1 (raw pcm) 2 (ADPCM) 3 (IEEE Float) 6 (g711a) 7 (g711u)
numCh: 
dataLength: wav
**/
Recorder.wav_header=function(format,numCh,sampleRate,bitRate,dataLength){
	// http://soundfile.sapp.org/doc/WaveFormat/ https://www.jianshu.com/p/63d7aa88582b https://github.com/mattdiamond/Recorderjs https://www.cnblogs.com/blqw/p/3782420.html https://www.cnblogs.com/xiaoqi/p/6993912.html
	var extSize=format==1?0:2;
	var buffer=new ArrayBuffer(44+extSize);
	var data=new DataView(buffer);
	
	var offset=0;
	var writeString=function(str){
		for (var i=0;i<str.length;i++,offset++) {
			data.setUint8(offset,str.charCodeAt(i));
		};
	};
	var write16=function(v){
		data.setUint16(offset,v,true);
		offset+=2;
	};
	var write32=function(v){
		data.setUint32(offset,v,true);
		offset+=4;
	};
	
	/* RIFF identifier */
	writeString('RIFF');
	/* RIFF chunk length */
	write32(36+extSize+dataLength);
	/* RIFF type */
	writeString('WAVE');
	/* format chunk identifier */
	writeString('fmt ');
	/* format chunk length */
	write32(16+extSize);
	/* audio format */
	write16(format);
	/* channel count */
	write16(numCh);
	/* sample rate */
	write32(sampleRate);
	/* byte rate (sample rate * block align) */
	write32(sampleRate*(numCh*bitRate/8));// *1 
	/* block align (channel count * bytes per sample) */
	write16(numCh*bitRate/8);// *1 
	/* bits per sample */
	write16(bitRate);
	if(format!=1){// ExtraParamSize 0
		write16(0);
	}
	/* data chunk identifier */
	writeString('data');
	/* data chunk length */
	write32(dataLength);
	
	return new Uint8Array(buffer);
};

}));